.buttonOrange {
    text-transform: none;
    text-decoration: none;
    background: linear-gradient(254deg, #35693E 10%, #6bb314 100%) 0% 0% no-repeat padding-box padding-box transparent;
    border-radius: 32px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;
    font: 500 16px / 25px Poppins;
    letter-spacing: 0px;
    color: rgb(255, 255, 255);
    box-shadow: #6bb314 0px 8px 36px;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 768px){
    .buttonOrange {
        font: 500 14px / 25px Poppins;
    }
}
@media (max-width: 320px){
    .buttonOrange {
        font: 500 .7rem Poppins;
    }
}